import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { ApiApplicationService } from 'src/app/services/api-application.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';

@Component({
  selector: 'app-track-application',
  templateUrl: './track-application.page.html',
  styleUrls: ['./track-application.page.scss'],
})
export class TrackApplicationPage implements OnInit {
  applicationID=0;
  arrTracker = [];

  constructor(
    private apiApplicationService : ApiApplicationService,
    private route : ActivatedRoute,
    private headerTitle : HeaderTitleService,
  ) { 
    this.headerTitle.title = "Track Application";
    this.headerTitle.isBack = true;
  }

  ngOnInit() {
    this.applicationID = this.route.snapshot.params['appID'];
    this.getTracker();
    
  }


  getTracker() {
    let postData = '{"ApplicationId":' + this.applicationID + ',"IsActive":true }';
    this.apiApplicationService.GetStageTrackApplication(postData).then(resp => {
      if (resp.length > 0) {
        this.arrTracker = resp;        
      }
    })
  }
}
